import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { GeneralService } from 'src/app/services/general.service';
import { UserService } from 'src/app/services/user.service';
import Swal from 'sweetalert2';


@Component({
  selector: 'app-listado-facturas-orden-compras-bcvusuarios-clientes-public',
  templateUrl: './listado-facturas-orden-compras-bcvusuarios-clientes-public.component.html',
  styleUrls: ['./listado-facturas-orden-compras-bcvusuarios-clientes-public.component.css']
})
export class ListadoFacturasOrdenComprasBCVUsuariosClientesPublicComponent implements OnInit {

  compras: any = [];
  filter: '';
  paginate = 1;
  usuarios: any = [];

  constructor(
    private auth: UserService,
    private router: Router,
    private generalService: GeneralService,
    private activatedRoute: ActivatedRoute
    
  ) { }

  ngOnInit(): void {
    this.informacionUsuario();
    this.getOrdenCompraPorBCV();
  }




  getOrdenCompraPorBCV()
  {
    const params = this.activatedRoute.snapshot.params;
    this.generalService.getOrdenCompraPorBCV(params.id).subscribe(res => {
      this.compras = res;
      console.log(res);
    },
    err => {
      console.log(err);
      if (err.status === 0)
      {
        Swal.fire(
          'Error!',
          'El servidor no esta activo, no podra ver los datos...',
          'error'
        );
      }
    }
   );
  }





  informacionUsuario() {
    this.auth.profile().subscribe(
      res => {
       this.usuarios = res;
       console.log('usuario data', res);

      
      },
     err => {
        alert('Sesion Experiada..');
        this.router.navigateByUrl('/login');
      }
    );
  }
}
