import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import Swal from 'sweetalert2';
import { UserService } from 'src/app/services/user.service';
import { GeneralService } from 'src/app/services/general.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { param } from 'jquery';


@Component({
  selector: 'app-orden-compras-en-esperas-form-admin-bcv-local',
  templateUrl: './orden-compras-en-esperas-form-admin-bcv-local.component.html',
  styleUrls: ['./orden-compras-en-esperas-form-admin-bcv-local.component.css']
})
export class OrdenComprasEnEsperasFormAdminBcvLocalComponent implements OnInit {

  compras: any = [];
  filter: '';
  paginate = 1;
  usuarios: any = [];
  codigo: any = '';

  estatusGuardar = 0;


  Form: FormGroup;


  createFormGroup() {
    return new FormGroup({
      producto_id: new FormControl('', [Validators.required]),
      estatu_id: new FormControl('', [Validators.required]),
      user_id: new FormControl('', [Validators.required] ),
      codigo_bcv: new FormControl('', [Validators.required] ),
        
    });
  }



  Form2: FormGroup;


  createFormGroup2() {
    return new FormGroup({
      user_id: new FormControl('', [Validators.required] ),
      codigo_bcv: new FormControl('', [Validators.required] ),
      cedula_compra: new FormControl('', [Validators.required] ),
        
    });
  }











  constructor(
    private auth: UserService,
    private router: Router,
    private generalService: GeneralService,
    private activatedRoute: ActivatedRoute
    
  ) {
    this.Form = this.createFormGroup();
    this.Form2 = this.createFormGroup2();
  
  }

  ngOnInit(): void {
    this.informacionUsuario();
    this.getMisComprasUsuarios();
  }

  Actualizar()
  {
    this.getMisComprasUsuarios();
    this.estatusGuardar = 1;
  }

  GuardarFactura()
  {
      this.generalService.saveFacturasCompras(this.Form2.value).subscribe(
      res => {
            console.log(res);
            this.router.navigate(['/orden_compra/admin/bcv/list/espera']);
            Swal.fire(
              'Exitoso!',
              'Factura Guardada correctamente...!',
              'success'
            );
      },
      err => console.error(err)
    );
  }


  getMisComprasUsuarios()
  {
    const params = this.activatedRoute.snapshot.params;
    this.generalService.getOrdenUsuariosListadoEnEspera(params.id).subscribe(res => {
      this.compras = res;
      console.log('compras', this.compras);
      
     let usuario_id = params.id;

     this.Form2.get('user_id').setValue(usuario_id);
     this.Form2.get('codigo_bcv').setValue(this.compras[0].codigo_bcv);
     this.Form2.get('cedula_compra').setValue(this.compras[0].cedula_compra);

      //console.log('usuario compra:', this.compras[0].codigo_bcv);
    },
    err => {
      console.log(err);
      if (err.status === 0)
      {
        Swal.fire(
          'Error!',
          'El servidor no esta activo, no podra ver los datos...',
          'error'
        );
      }
    }
   );
  }




  Confirmar(compra_id) {
  
    this.Form.get('estatu_id').setValue(2);
    


    this.generalService.updateOrdens(compra_id, this.Form.value)
      .subscribe(
        res => {
          console.log(res);
         
         
          //this.router.navigate(['/empresas/admin/list']);
          Swal.fire(
            'Exitoso!',
            'Datos actualizados correctamente...!',
            'success'
          );
        },
        err => console.error(err)
      );
  }



  Rechazar(compra_id) {
  
    this.Form.get('estatu_id').setValue(3);
    this.Form.get('codigo_bcv').setValue(0);

    this.generalService.updateOrdens(compra_id, this.Form.value)
      .subscribe(
        res => {
          console.log(res);
          //this.getMisComprasUsuarios();
          //this.router.navigate(['/empresas/admin/list']);
          Swal.fire(
            'Exitoso!',
            'Datos actualizados correctamente...!',
            'success'
          );
        },
        err => console.error(err)
      );
  }









  informacionUsuario() {
    this.auth.profile().subscribe(
      res => {
       this.usuarios = res;
       console.log('usuario data', res);
       this.Form.get('user_id').setValue(this.usuarios.user.id);

      
      },
     err => {
        alert('Sesion Experiada..');
        this.router.navigateByUrl('/login');
      }
    );
  }


}
