import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { GeneralService } from 'src/app/services/general.service';
import { UserService } from 'src/app/services/user.service';
import Swal from 'sweetalert2';
import { FormBuilder, FormGroup, FormControl, Validators, FormArray } from '@angular/forms';




@Component({
  selector: 'app-solicitar-beneficiario-clientes-public',
  templateUrl: './solicitar-beneficiario-clientes-public.component.html',
  styleUrls: ['./solicitar-beneficiario-clientes-public.component.css']
})
export class SolicitarBeneficiarioClientesPublicComponent implements OnInit {
   familiares: any = [];
   usuarios: any = [];
   tipo_beneficiario: any = true;
   familiar_cedula: any = '';
   estatusParentesco = true

  constructor(
    private auth: UserService,
    private router: Router,
    private generalService: GeneralService
  ) { }

  ngOnInit(): void {
    this.getFamiliares()
    this.informacionUsuario();
  }

  informacionUsuario() {
    this.auth.profile().subscribe(
      res => {
       this.usuarios = res;
       console.log('usuario data', res);

      
      },
     err => {
        alert('Sesion Experiada..');
        this.router.navigateByUrl('/login');
      }
    );
  }


  tipoBeneficiario()
  {
    if(this.tipo_beneficiario == 'titular') 
    {
      this.estatusParentesco = true;
      //alert('titular');
    }

    else if(this.tipo_beneficiario == 'familiar') 
    {
      this.estatusParentesco = false
     //alert('familiar');
    }
  }
  

  getFamiliares()
  {
    this.generalService.getFamiliares().subscribe(res => {
      this.familiares = res;
      console.log(res);
    },
    err => {
      console.log(err);
      if (err.status === 0)
      {
        Swal.fire(
          'Error!',
          'El servidor no esta activo, no podra ver los datos...',
          'error'
        );
      }
    }
   );
  }

}
