import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { UsuariosFormAdminComponent } from './components/admin/usuarios/usuarios-form-admin/usuarios-form-admin.component';
import { UsuariosListAdminComponent } from './components/admin/usuarios/usuarios-list-admin/usuarios-list-admin.component';
import { InicioPublicGeneralComponent } from './components/public/general/inicio-public-general/inicio-public-general.component';
import { LoginPublicComponent } from './components/public/general/login-public/login-public.component';
import { InicioAdminComponent } from './components/admin/inicio-admin/inicio-admin.component';
import { SolicitarBeneficiarioClientesPublicComponent } from './components/public/clientes/solicitar-beneficiario-clientes-public/solicitar-beneficiario-clientes-public.component';
import { OrdenCompraClientesEsperaPublicComponent } from './components/clientes/compras/orden-compra-clientes-espera-public/orden-compra-clientes-espera-public.component';
import { OrdenComprasEnEsperasListadoAdminBcvLocalComponent } from './components/bcv/orden-compras-en-esperas-listado-admin-bcv-local/orden-compras-en-esperas-listado-admin-bcv-local.component';
import { OrdenComprasEnEsperasFormAdminBcvLocalComponent } from './components/bcv/orden-compras-en-esperas-form-admin-bcv-local/orden-compras-en-esperas-form-admin-bcv-local.component';
import { ListadoFacturasOrdenComprasFarmaciasAceptadasComponent } from './components/farmacias/adminlocal/listado-facturas-orden-compras-farmacias-aceptadas/listado-facturas-orden-compras-farmacias-aceptadas.component';
import { FormFacturasOrdenComprasFarmaciasAceptadasComponent } from './components/farmacias/adminlocal/form-facturas-orden-compras-farmacias-aceptadas/form-facturas-orden-compras-farmacias-aceptadas.component';
import { MiListadoMedicinasClientesPublicComponent } from './components/public/clientes/compras/mi-listado-medicinas-clientes-public/mi-listado-medicinas-clientes-public.component';
import { ListadoFacturasUsuariosClientesPublicComponent } from './components/public/clientes/facturas/listado-facturas-usuarios-clientes-public/listado-facturas-usuarios-clientes-public.component';
import { ListadoFacturasOrdenComprasBCVUsuariosClientesPublicComponent } from './components/public/clientes/ordenes/listado-facturas-orden-compras-bcvusuarios-clientes-public/listado-facturas-orden-compras-bcvusuarios-clientes-public.component';

const routes: Routes = [
  { path: 'inicio', component:  InicioPublicGeneralComponent },
  { path: 'admin/inicio', component:  InicioAdminComponent },
  { path: 'login', component:  LoginPublicComponent },




  { path: 'solicitar_beneficiario/clientes/form', component:  SolicitarBeneficiarioClientesPublicComponent },

  //clientes

  { path: 'orden_compra/public/clientes/list/:id', component:  MiListadoMedicinasClientesPublicComponent },

  { path: 'orden_compra/admin/local/list/espera/:id', component:  OrdenCompraClientesEsperaPublicComponent },


  { path: 'orden_compra/admin/bcv/list/espera', component:  OrdenComprasEnEsperasListadoAdminBcvLocalComponent },
  { path: 'orden_compra/admin/bcv/form/espera/:id', component:  OrdenComprasEnEsperasFormAdminBcvLocalComponent },



  //Factura Compras Farmacias
  { path: 'factura_compras/admin/local/list/aceptada', component:  ListadoFacturasOrdenComprasFarmaciasAceptadasComponent },
  { path: 'factura_compras/admin/local/form/aceptada/:id', component:  FormFacturasOrdenComprasFarmaciasAceptadasComponent },

  { path: 'factura_compras/clientes/listado/:id', component:  ListadoFacturasUsuariosClientesPublicComponent },
  { path: 'orden_compras/clientes/listado/bcv/:id', component:  ListadoFacturasOrdenComprasBCVUsuariosClientesPublicComponent },


  //USUARIOS //
  { path: 'usuarios/admin/list', component:  UsuariosListAdminComponent },
  { path: 'usuarios/admin/form', component:  UsuariosFormAdminComponent },
  { path: 'usuarios/admin/edit/:id', component:  UsuariosFormAdminComponent },


  { path: '**', pathMatch: 'full', redirectTo: 'inicio' }



];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
