import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { GeneralService } from 'src/app/services/general.service';
import { UserService } from 'src/app/services/user.service';
import Swal from 'sweetalert2';
import { FormBuilder, FormGroup, FormControl, Validators, FormArray } from '@angular/forms';



@Component({
  selector: 'app-orden-compra-clientes-espera-public',
  templateUrl: './orden-compra-clientes-espera-public.component.html',
  styleUrls: ['./orden-compra-clientes-espera-public.component.css']
})
export class OrdenCompraClientesEsperaPublicComponent implements OnInit {

  productos: any = [];
  ordenes: any = [];
  filter: '';
  paginate = 1;
  usuarios: any = [];
  listado: any = [];

  Form: FormGroup;


  createFormGroup() {
    return new FormGroup({
      producto_id: new FormControl('', [Validators.required]),
      estatu_id: new FormControl('', [Validators.required]),
      user_id: new FormControl('', [Validators.required]),
      cedula_compra: new FormControl('', [Validators.required]),
      precio: new FormControl('', [Validators.required] ),
      cantidad: new FormControl('', [Validators.required] ),
        
    });
  }


  constructor(
    private auth: UserService,
    private router: Router,
    private generalService: GeneralService,
    private activatedRoute: ActivatedRoute,
  ) { this.Form = this.createFormGroup(); }

  ngOnInit(): void {
    this.getProductos();
    this.informacionUsuario();
    this.MiListadoProductos();
  }






  MiListadoProductos()
  {
    this.generalService.getOrdenEnEsperaUsuarioCompra().subscribe(res => {
      this.listado = res;
      console.log(res);
    },
    err => {
      console.log(err);
      if (err.status === 0)
      {
        Swal.fire(
          'Error!',
          'El servidor no esta activo, no podra ver los datos...',
          'error'
        );
      }
    }
   );
  }


  EliminarOrden(orden_id)
  {
    
    if (confirm('Desea eliminar el producto?')) 
    {
      // si
      this.generalService.deleteOrdens(orden_id).subscribe(res => {
        console.log(res);
        Swal.fire(
          'Eliminado!',
          'Producto Eliminado...',
          'error'
        );
        this.MiListadoProductos();
     },
     err => {
       console.log(err);
       if (err.status === 0)
       {
         Swal.fire(
           'Error!',
           'El servidor no esta activo, no podra ver los datos...',
           'error'
         );
       }
     }
    );

  console.log('Eliminando');
    } else 
    {
      // Do nothing!
      console.log('No hizo nada');
    }

  }









  Guardar(compra_id) 
  {
    const params = this.activatedRoute.snapshot.params;
    let cedula_compra = params.id;
    this.Form.get('cedula_compra').setValue(cedula_compra); 
    this.Form.get('producto_id').setValue(compra_id);
  // alert(compra_id)
    this.generalService.saveOrdens(this.Form.value).subscribe(
      res => {
            console.log(res);
            this.getProductos();
            this.MiListadoProductos();
            this.filter= '';
            // this.router.navigate(['/productos/admin/list']);
            Swal.fire(
              'Exitoso!',
              'Productos agregados correctamente...!',
              'success'
            );
      },
      err => console.error(err)
    );
  }











  informacionUsuario() {
    this.auth.profile().subscribe(
      res => {
       this.usuarios = res;
       console.log('usuario data', res);
       this.Form.get('user_id').setValue(this.usuarios.user.id);

      
      },
     err => {
        alert('Sesion Experiada..');
        this.router.navigateByUrl('/login');
      }
    );
  }


  getProductos()
  {
    this.generalService.getProductos().subscribe(res => {
      this.productos = res;
      console.log(res);
     //this.Form.get('producto_id').setValue(this.productos[1].id);
      this.Form.get('estatu_id').setValue(1);
      
     // alert(this.productos[1].id)
    },
    err => {
      console.log(err);
      if (err.status === 0)
      {
        Swal.fire(
          'Error!',
          'El servidor no esta activo, no podra ver los datos...',
          'error'
        );
      }
    }
   );
  }

}
