import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import Swal from 'sweetalert2';
import { UserService } from 'src/app/services/user.service';
import { GeneralService } from 'src/app/services/general.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';


@Component({
  selector: 'app-form-facturas-orden-compras-farmacias-aceptadas',
  templateUrl: './form-facturas-orden-compras-farmacias-aceptadas.component.html',
  styleUrls: ['./form-facturas-orden-compras-farmacias-aceptadas.component.css']
})
export class FormFacturasOrdenComprasFarmaciasAceptadasComponent implements OnInit {

  compras: any = [];
  filter: '';
  paginate = 1;
  usuarios: any = [];
  user = 0;



  Form: FormGroup;


  createFormGroup() {
    return new FormGroup({
      cantidad: new FormControl('', [Validators.required]),
      precio: new FormControl('', [Validators.required]),
      estatu_id: new FormControl('', [Validators.required]),
      codigo_bcv: new FormControl('', [Validators.required]),
             
    });
  }








  Form2: FormGroup;

  createFormGroup2() {
    return new FormGroup({
      numero_factura: new FormControl('', [Validators.required]),
      precio_total: new FormControl('', [Validators.required]),
              
    });
  }



  constructor(
    private auth: UserService,
    private router: Router,
    private generalService: GeneralService,
    private activatedRoute: ActivatedRoute
  ) {
     this.Form = this.createFormGroup();
     this.Form2 = this.createFormGroup2(); 
    }

  ngOnInit(): void {
    this.informacionUsuario();
    this.getMisComprasUsuarios();
  }



  getMisComprasUsuarios()
  {
   
   
    
     const params = this.activatedRoute.snapshot.params;
    this.generalService.getOrdenUsuariosListadoEnAceptado(params.id).subscribe(res => {
      this.compras = res;
      console.log('compras', this.compras);
      this.Form.get('codigo_bcv').setValue(this.compras[0].codigo_bcv);
     
      
     let usuario_id = params.id;

     //this.Form2.get('user_id').setValue(usuario_id);
    
     //this.Form2.get('cedula_compra').setValue(this.compras[0].cedula_compra);

      //console.log('usuario compra:', this.compras[0].codigo_bcv);
    },
    err => {
      console.log(err);
      if (err.status === 0)
      {
        Swal.fire(
          'Error!',
          'El servidor no esta activo, no podra ver los datos...',
          'error'
        );
      }
    }
   );
  }


  ActualizarFactura(){
  
         
     
      
       const params = this.activatedRoute.snapshot.params;
      this.generalService.getOrdenUsuariosListadoEnAceptado(params.id).subscribe(res => {
        this.compras = res;
        console.log('compras', this.compras);
        this.Form.get('codigo_bcv').setValue(this.compras[0].codigo_bcv);
           
      });

  

    this.generalService.updateFacturasCompras(this.compras[0].id_factura, this.Form2.value)
      .subscribe(
        res => {
          console.log(res);
         
         
          //this.router.navigate(['/empresas/admin/list']);
          Swal.fire(
            'Exitoso!',
            'Factura actualizados correctamente...!',
            'success'
          );
        },
        err => console.error(err)
      );
  }


  Confirmar(compra_id) {
  
    this.Form.get('estatu_id').setValue(4);
   
    


    this.generalService.updateOrdens(compra_id, this.Form.value)
      .subscribe(
        res => {
          console.log(res);
         
         
          //this.router.navigate(['/empresas/admin/list']);
          Swal.fire(
            'Exitoso!',
            'Datos actualizados correctamente...!',
            'success'
          );
        },
        err => console.error(err)
      );
  }



  Rechazar(compra_id) {
  
    this.Form.get('estatu_id').setValue(3);
    this.Form.get('cantidad').setValue(0);
    this.Form.get('precio').setValue(0);

    this.generalService.updateOrdens(compra_id, this.Form.value)
      .subscribe(
        res => {
          console.log(res);
          //this.getMisComprasUsuarios();
          //this.router.navigate(['/empresas/admin/list']);
          Swal.fire(
            'Exitoso!',
            'Datos actualizados correctamente...!',
            'success'
          );
        },
        err => console.error(err)
      );
  }







  informacionUsuario() {
    this.auth.profile().subscribe(
      res => {
       this.usuarios = res;
       console.log('usuario data', res);
       this.Form.get('user_id').setValue(this.usuarios.user.id);
       this.user = this.usuarios.user.id;

      
      },
     err => {
        alert('Sesion Experiada..');
        this.router.navigateByUrl('/login');
      }
    );
  }

}
